.airdropView{
  padding: 50px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .claimBox{
    background: #E58F0E;
    border-radius: 20px;
    padding: 30px 20px;
    width: 350px;
    max-width: 100%;
    margin-bottom: 50px;

    h1{
      text-align: center;
      color: #49221A;
      font-size: 24px;
      font-weight: 700;
    }

    .unlockedAmount{
      margin: 20px 0;
      border-bottom: 1px dashed #471704;
      padding-bottom: 20px;
      .itemRow{
        display: flex;
        align-items: center;
        justify-content: space-between;
        color:#49221A;
        font-weight: 600;
        margin-bottom: 10px;
        font-size: 15px;
        img{
          width: 25px;
          margin-right: 5px;
          border: 1px solid #00DEFF;
          border-radius: 32px;
          background: #000000;
        }
      }
    }
    .button{
      font-weight: bold;
      color: #49221A;
      margin-top: 20px;
      border: 2px solid #731C00;
      border-radius: 40px;
      padding: 10px 0;
      text-align: center;
      cursor: pointer;
    }
  }

  .ruleBox{
    width: 700px;
    max-width: 100%;
    padding: 20px;
    color: white;
    background: #401B0B;
    border: 2px solid #401B0B;
    border-radius: 20px;
    p{
      font-size: 14px;
      margin-bottom: 15px;
      line-height: 20px;
      text-align: justify;
    }

    .chart{
      color: #FEBF33;
      margin: 30px 0;
      .row{
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #FEBF33;
        font-weight: bold;
        font-size: 16px;
        padding-bottom: 10px;
        margin-bottom: 10px;
        border-bottom: 1px solid #ffffff2b;
      }
    }
    .button{
      padding: 10px 0;
      background: #FEBF33;
      border: 2px solid #731C00;
      border-radius: 40px;
      text-align: center;
      width: 250px;
      color: #401B0B;
      font-weight: bold;
      cursor: pointer;
    }
  }

  @media(max-width: 765px){
    .ruleBox{
      .chart{
        .row{
          font-size: 12px;
          font-weight: 600;
        }
      }
    }
  }
}