.investment{
  margin-top: 30px;

  .clickableButton{
    border: 2px solid #731C00;
    box-sizing: border-box;
    border-radius: 40px;
    padding: 10px 0;
    font-size: 18px;
    font-weight: bold;
    color:#49221A;
    cursor: pointer;
  }
  .cardDisplay{
    margin-bottom: 30px;
    background: #401B0B;
    border: 1px solid #E58F0E;
    box-sizing: border-box;
    border-radius: 20px;
    padding: 20px;
  }
  .spacingM{
    height: 125px;
    position: relative;
  }
  .middleWords{
    text-align: center;
    font-weight: 400;
    color: white;
    font-size: 24px;
    margin-bottom: 0px;
  }

  .verticalTextContainer {
      display: inline-block;
      width: 50%;
      vertical-align: middle;
  }

  .purchaseBox{
    height: auto;
    position: relative;

    .bgImg{
      width: 50%;
      height: 280px;
      position: relative;
      display: inline-block;
      vertical-align: middle;
      padding: 0;
      img{
        height: 250px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
    h2{
      color: #49221A;
    }

    .ant-input-affix-wrapper {
      height: 55px;
      margin: 0;
      position: relative;
      border: 2px solid #731C00;
      box-shadow: none;
      backdrop-filter: blur(20px);
      box-sizing: border-box;
      border-radius: 40px;
      background: #C6660D;
      .ant-input-suffix,
      .ant-input-prefix {
        color: #FEEC6C;
        font-size: 16px;
        font-weight: 600;
        cursor: pointer;
        user-select: none;
        span {
          color: #FEEC6C;
        }
      }
      .suffix {
        padding-right: 10px;
      }

      .ant-input {
        border: none;
        color: #FEEC6C;
        background: transparent;
        font-size: 16px;
        font-weight: 600;
        line-height: 55px;
        text-align: center;
        padding-left: 25px;
      }

      .ant-input:hover {
        border: none !important;
      }
      .ant-input:focus {
        border: none !important;
        outline: 0;
        box-shadow: none !important;
      }
    }

    .ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover{
      border-color: #731C00 !important;
      border-right-width: 2px;
    }

    .ant-input-affix-wrapper-focused {
      outline: 0;
      border: 2px solid #731C00 !important;
      box-shadow: none !important;
    }


  }

  .tabSelector{
    display: flex;
    justify-content: center;
    gap: 50px;
    margin-bottom: 20px;
    div{
      font-size: 25px;
      color: #FEBF33;
      font-weight: bold;
      border-bottom: 3px solid transparent;
      cursor: pointer;
      &.activeTab, &:hover{
        border-bottom: 3px solid #E58F0E;
      }
    }
  }

  .purchaseBox h2{
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 25px;
  }

  .purchaseBox .youWillGet{
    margin-bottom: 15px;
    font-size: 14px;
    font-weight: 400;
    min-height: 25px;
  }
  .investNotification{
    color:white;
    margin-bottom: 0;
    font-size: 13px;
    font-weight: 500;
  }

  .ant-notification{
    width: auto !important;
  }
  .ant-notification-notice{
    padding: 7px 15px !important;
    margin-bottom: 5px !important;
    /* background: transparent !important; */
    background: #68caf5 !important;
    border-top-left-radius: 0 !important;
    border-bottom-left-radius: 0 !important;
  }
  .ant-notification-notice-message{
    margin: 0 !important;
    padding-right: 0 !important;
  }

  .ant-notification-notice-close{
    display: none !important;
  }
  .ant-notification-topLeft, .ant-notification-bottomLeft{
    margin-left: 0px !important;
  }

  .ant-input-group-addon,.ant-input {
    background-color: transparent;
    color: white;
  }

  .redButton{
    background: #FF76C2 !important;
    border: solid 1px #FF76C2 !important;
  }

  .greenButton{
    margin-top: 40px;
    background: #8ADE47;
    border: solid 1px #8ADE47;
    color: #fff;
    padding: 7px 25px;
    text-transform: capitalize;
    border-radius: 2px;
    margin-bottom: 10px;
    outline: none;
    box-shadow: none;
    border: none;
    display: block;
    width: 80%;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.43;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    transition: all 0.3s ease;
  }

  .clickableButton{
      margin-top: 20px;
      margin-bottom: 10px;
  }

  .balance{
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 400;
    margin-top: 20px;
    border-top: 1px dashed #731C00;
    padding-top: 10px;
    color: #49221A;
  }

  .priceTitle{
    font-size: 14px;
    font-weight: bold;
    color: white;
    margin-top: 20px;
    margin-bottom: 0;
    line-height: 25px;
  }

  .priceTag{
    font-size: 14px;
    font-weight: 400;
    color: white;
    margin-bottom: 10px;
  }

  .priceValue{
    color: white;
  }
  .purchaseBox .ant-input-group{
    height: 40px !important;
  }

  .purchaseBox .ant-input-group .ant-input{
    height: 40px !important;
  }
  .addonAfter{
    margin: 0;
    padding: 0 10px;
  }
  .withdrawTop{
    min-height: 200px;

    h1{
      font-weight: 400;
      font-size: 17px;
      color: white;
      margin: 0 0 20px;
      text-align: center;
    }
    img{
      width: 50%;
    }
  }

  .status{
    color: #282633;
    border-bottom: 1px solid #3f3c48;
    margin: 20px auto;
  }

  .status .investPrompt p{
    color: #0D1E77;
    font-size: 16px;
    font-weight: 500;
  }

  .status .statusPrompt{
    color: #444 !important;
    font-size: 20px !important;
  }

  .status .statSection{
    margin-top: 30px;
    color: #333;
    font-weight: 500;
  }

  .status .statSection h2{
    color: #444;
    font-size: 18px;
  }
  .status .statSection p{
    font-size: 18px;
    margin-bottom: 20px;
    color: #74cef9;
  }
  .launchingTitle{
    font-size: 25px;
    text-align: center;
    margin: 0;
  }

  .max{
    font-size: 16px;
    padding: 5px 10px;
    border-radius: 5px;
  }

  .max:hover{
    cursor: pointer;
  }

  @media(max-width:765px){
    .purchaseBox{
      height: auto;
      .bgImg{
        display: none;
      }
    }
    .verticalTextContainer{
      width: 100%;
    }
  }
}
