
.token{
  padding-top: 50px;
  position: relative;
  color: white;
  .innerWrap{
    width: 800px;
    margin: 0 auto;
    max-width: 100%;
  }
  .banner{
    .promptSection{
      h1{
        color: #FEBF33;
        font-size: 30px;
        margin-bottom: 0;
        line-height: 40px;
      }
      h2{
        color: #FEBF33;
        font-size: 20px;
        margin-bottom: 0;
      }
      p{
        color: #FEBF33;
        opacity: 0.7;
        font-size: 15px;
        margin: 0;
      }
    }
  }

  .roastTime{
    position: absolute;
    right: 10px;
    bottom: 10px;
    background: #FEBF33;
    border-radius: 20px;
    padding: 0 10px;
    color: #401B0B;
    letter-spacing: -1px;
    font-size: 11px;
  }
  .dataContainer{
    width: 100%;
    max-width: 100%;
    margin-top: 40px;
    position: relative;
    .dataBoxWrap{
      position: relative;
      width: calc(25% - 10px);
      display: inline-block;
      margin: 0 5px 15px 5px;
      vertical-align: middle;
      .rainbowWrap {
        position: absolute;
        width: calc(100% + 30px);
        height: calc(100% + 2px);
        z-index: 1;
        padding: 0 12px;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }

      .rainbow {
        border-radius: 15px;
          width: 100%;
          height: 100%;
          //filter: blur(4px);
          //background: linear-gradient(45deg,red,#ff9a00 10%,#d0de21 20%,#4fdc4a 30%,#3fdad8 40%,#2fc9e2 50%,#1c7fee 60%,#5f15f2 70%,#ba0cf8 80%,#fb07d9 90%,red);
      }

      .dataBox{
        background: #401B0B;
        border: 1px solid #E58F0E;
        box-sizing: border-box;
        border-radius: 20px;
        padding: 20px 0 20px 20px;
        vertical-align: top;
        position: relative;
        z-index: 2;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        text-align: left;
        img{
          width: 18%;
          margin-right: 15px;
        }

        h2{
          letter-spacing: 0;
          font-size: 18px;
          font-weight: 600;
          margin-bottom: 0;
          color: #FEBF33;
        }

        p{
          height: 21px;
          line-height: 21px;
          color: #FEBF33;
        }


      }
    }

  }


  .contractAddress{
    width: 500px;
    max-width: 100%;
    border:2px solid #FF76C2;
    border-radius: 10px;
    margin: 15px auto 0 auto !important;
    padding: 10px 10px;

    h1{
      color: white;
      font-size: 18px !important;
      font-weight: 600;
    }

    .addressDiv{
      position: relative;
      height: 40px;
      border: 1px solid white;
      border-radius: 10px;
      padding: 5px;
      .vipMinerAddress{
        width: calc(100% - 110px);
        text-align: left;
        display: inline-block;
        color: white;
        height: 30px;
        line-height: 30px;
        max-width: calc(100% - 110px);
        text-overflow: ellipsis;
        overflow-x: hidden;
        white-space: nowrap;
        position: absolute;
        top: 50%;
        left: 10px;
        cursor: pointer;
        text-decoration: underline;
        transform: translateY(-50%);
      }

      .copy{
        cursor: pointer;
        position: absolute;
        top: 50%;
        right: 10px;
        transform: translateY(-50%);
        display: inline-block;
        width:80px;
        height: 42px;
        text-align: center;
        color: white;
        background-color: #8ADE47;
        border-radius: 10px;
        height: 30px;
        line-height: 30px;
      }
    }




  }

  .faqHeader{
    margin-top: 50px;
    h1{
      font-size: 25px;
      font-weight: 700;
      margin-bottom: 10px;
      text-align: center;
      color: #E58F0E;
    }
    p{
      font-size: 16px;
      text-align: center;
      color: #E58F0E;
    }
  }

  @media(max-width: 765px){
    .walletSection{
      top: 20px;
      position: fixed;
      z-index: 5;
    }
    .dataContainer{
      margin-top: 30px;
      .dataBoxWrap{
        width: calc(100%);
        margin: 5px 0;
        .dataBox{
          img{
            width: 50px;
            margin-right: 20px;
          }

          h2{
            letter-spacing: 0;
            font-size: 20px;
            font-weight: bold;
            margin-bottom: 0;
            color: #FEBF33;
          }

          p{
            height: 21px;
            line-height: 21px;
            color: #FEBF33;
          }


        }
      }

    }
  }
}

.walletSection{
  position: absolute;
  top: 0;
  right: 0;
}
.walletSection .ethBalance{
  padding-right: 10px;
  font-size: 10px;
}
.walletSection .walletAddress{
  font-size: 14px;
  margin-bottom: 0;
  background-color: #FBBA08;
  color: black;
  padding: 5px 10px 5px 18px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  font-weight: 500;
}

.greenButton{
  background-color: #4BC071;
  color: white;
  padding: 8px 30px;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 600;
  overflow: hidden;
  white-space: initial;
}

.redButton{
  background-color: #E54636;
  color: white;
  padding: 8px 30px;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 600;
  overflow: hidden;
  white-space: initial;
}

.investPrompt p{
  color: #0D1E77;
  font-size: 20px;
  font-weight: 600;
}


.tokenBackground h1{
  font-weight: 500;
  font-size: 19px;
  color: #FFFFFF;
  margin-top: 60px;
  margin-bottom: 0;
}
.tokenBackground h1:hover{
  text-decoration: underline;
}

.titleHover h2:hover{
  text-decoration: underline;
}
.tokenBackground span{
  font-size: 25px;
}
.tokenBackground img{
  /* padding-top: 8px; */
}

.tokenBackground h2{
  margin-bottom: 0;
  font-weight: 900;
  color: #FFFFFF;
  font-size: 25px;
}
.tokenBackground h4{
  margin-bottom: 0;
  font-family: OpenSans-Semibold;
  font-weight: 400;
  color: #FFFFFF;
}
