.faq{
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  h1{
    font-size: 30px;
    font-weight: 700;
  }

  .faqItemBox{
    width: calc(50% - 20px);
    border: 1px solid #E58F0E;
    box-sizing: border-box;
    border-radius: 20px;
    padding: 30px;
    margin-bottom: 10px;
    img{
      height: 50px;
      width: auto;
      display: block;
      margin: 0 auto;
    }
  }

  .question{
    text-align: center;
    font-size: 18px;
    font-weight: 650;
    margin:15px 0 8px 0;
    color: #E58F0E;
  }

  .answer{
    text-align: justify;
    font-weight: 400;
    color: #E58F0E;
    font-size: 14px;

    p{
      margin-bottom: 0;
    }
  }

  @media (max-width:800px){
    padding: 30px 5%;

    .faqItemBox{
      width: calc(100%);
    }
    div{
      padding: 0;
    }

    h1{
      font-size: 20px;
      font-weight: 650;
    }

    .question{
      font-size:14px;
      font-weight: 600;
    }

    .answer{
      font-size: 12px;
      font-weight: 500;
    }

  }

}
/**
* FAQ section end
*/
